import { io } from 'socket.io-client'

let websocket: any

export const connectWebSocket = () => {
  const url =
    window.location.host.includes('localhost') || window.location.host.includes('192.168')
      ? 'http://localhost:5500'
      : 'https://my-node-app-fzlkp5xhga-de.a.run.app:5050'

  websocket = io(url)

  websocket.on('connect', () => {
    console.log('success connect Websocket')
  })

  websocket.on('disConnection', () => {
    websocket.close()
  })

  websocket.on('websocket', (message: any) => {
    console.log('websocket', message)
  })
}

export const socket = () => {
  return websocket
}
